import { HocuspocusProvider } from '@hocuspocus/provider';
import CharacterCount from '@tiptap/extension-character-count';
import Collaboration from '@tiptap/extension-collaboration';
import CollaborationCursor from '@tiptap/extension-collaboration-cursor';
import Highlight from '@tiptap/extension-highlight';
import TaskItem from '@tiptap/extension-task-item';
import TaskList from '@tiptap/extension-task-list';
import StarterKit from '@tiptap/starter-kit';
import { Editor, EditorContent } from '@tiptap/vue-3';
import * as Y from 'yjs';
import ShareModal from '@/components/ShareModal';

//import { variables } from '../../../variables'
import MenuBar from '@/components/MenuBar.vue';
const getRandomElement = list => {
  return list[Math.floor(Math.random() * list.length)];
};
const getRandomRoom = () => {
  //const roomNumbers = variables.collabRooms?.trim()?.split(',') ?? [10, 11, 12]
  const roomNumbers = [...Array(99).keys()];
  return getRandomElement(roomNumbers.map(number => `rooms.${number}`));
};
export default {
  components: {
    EditorContent,
    MenuBar,
    ShareModal
  },
  data() {
    return {
      currentUser: JSON.parse(localStorage.getItem('currentUser')) || {
        name: this.getRandomName(),
        color: this.getRandomColor(),
        rooms: {}
      },
      provider: null,
      editor: null,
      status: 'connecting',
      room: getRandomRoom()
    };
  },
  created() {
    console.log("router", this.$router, this.$route);
    if (this.$route.query.room) {
      this.room = this.$route.query.room;
      console.log('room', this.room);
    }
  },
  mounted() {
    this.createEditor();
  },
  methods: {
    createEditor() {
      const ydoc = new Y.Doc();
      this.provider = new HocuspocusProvider({
        //appId: '7j9y6m10',
        url: 'wss://hocus-noosphere.glitch.me/',
        name: this.room,
        document: ydoc
      });
      this.provider.on('status', event => {
        this.status = event.status;
      });
      this.currentUser.rooms[this.room] = {
        room: this.room,
        date: Date.now()
      };
      //this.currentUser.clientID = this.awareness.clientID

      this.editor = new Editor({
        extensions: [StarterKit.configure({
          history: false
        }), Highlight, TaskList, TaskItem, Collaboration.configure({
          document: ydoc
        }), CollaborationCursor.configure({
          provider: this.provider,
          user: this.currentUser
        }), CharacterCount.configure({
          limit: 10000
        })]
      });
      this.editor.on('create', ({
        editor
      }) => {
        //  console.log(editor.getJSON())
        let step = {
          room: this.room,
          data: editor.getJSON(),
          type: 'tiptap'
        };
        this.$store.dispatch('push', step);
        // The content has changed.

        // let user = this.currentUser
        // user.room = this.room
        // delete user.rooms[this.room]
        // let room = {room: this.room, date : Date.now()}
        //user.rooms[this.room] = room

        this.$store.commit('setUser', this.currentUser);
        this.$store.commit('setRoom', this.room);

        // You can think of your own awareness information as a key-value store.
        // We update our "user" field to propagate relevant user information.
      });

      this.editor.on('update', ({
        editor
      }) => {
        //  console.log(editor.getJSON())
        let step = {
          room: this.room,
          data: editor.getJSON(),
          type: 'tiptap'
        };
        this.$store.dispatch('push', step);
        // this.roomsYmap.set(this.room, editor.storage.collaborationCursor)
        // this.rooms = this.roomsYmap
        // The content has changed.
      });

      localStorage.setItem('currentUser', JSON.stringify(this.currentUser));
      localStorage.setItem('currentRoom', JSON.stringify(this.room));
      let r = {
        room: this.room,
        users: this.editor.storage.collaborationCursor.users
      };
      this.$store.commit('updateRooms', r);
      //  this.$store.commit('setRoom', this.room)
    },

    setName() {
      const name = (window.prompt('Name') || '').trim().substring(0, 32);
      if (name) {
        return this.updateCurrentUser({
          name
        });
      }
    },
    setRoom() {
      const room = (window.prompt('Choose a Room from 0 to 99') || '').trim().substring(0, 32);
      if (room) {
        return this.updateCurrentRoom({
          room
        });
      }
    },
    updateCurrentUser(attributes) {
      this.currentUser = {
        ...this.currentUser,
        ...attributes
      };
      this.editor.chain().focus().updateUser(this.currentUser).run();
      localStorage.setItem('currentUser', JSON.stringify(this.currentUser));
    },
    updateCurrentRoom(attributes) {
      console.log(attributes);
      this.room = attributes.room.startsWith("rooms.") ? attributes.room : "rooms." + attributes.room;
      this.editor.destroy();
      this.provider.destroy();
      this.createEditor();
    },
    getRandomColor() {
      return getRandomElement(['#958DF1', '#F98181', '#FBBC88', '#FAF594', '#70CFF8', '#94FADB', '#B9F18D']);
    },
    getRandomName() {
      return getRandomElement(['Leon Thompson', 'Bonnie Journey', 'Thomas Crois√©', 'Malouna', 'Terry Ball', 'Jean Collik', 'Winoumanona Raider', 'Christina Pompin', 'Alycia Francisco', 'Moully Ringinald', 'B√©atrice Allou', 'Pierre Kiroul', 'Namass Pamouss', 'Etu Diante', 'Da Di Falavaleris', 'Dgef', 'Emilio Agricolo', 'Miss B', 'Spider Marvel', 'Hulkor Geeen', 'Smoke Onthewater', 'Bebop Euloula', 'Mick Mock', 'Peter Piter', 'Stocking Head']);
    }
  },
  beforeUnmount() {
    this.editor.destroy();
    this.provider.destroy();
  },
  watch: {
    roomFromSomewhere() {
      if (this.room != this.roomFromSomewhere) {
        let room = this.roomFromSomewhere;
        console.log(room);
        // if (room.rooms) this.updateCurrentRoom({room})
        this.updateCurrentRoom({
          room
        });
      }
    },
    collaborationUsers() {
      if (this.users != this.collaborationUsers) {
        console.log("collaborationUsers changed", this.collaborationUsers);
        this.$store.commit('setUsersInRoom', {
          room: this.room,
          users: this.collaborationUsers
        });
      }
    }
  },
  computed: {
    roomFromSomewhere() {
      return this.$store.state.room;
    },
    collaborationUsers() {
      return this.editor == null ? [] : this.editor.storage.collaborationCursor.users;
    },
    users() {
      return this.$store.state.users;
    }
  }
};